.slick-dots1
{
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.slick-dots1 li
{
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
}

.slick-dots1 li button
{
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots1 li button:hover,
.slick-dots1 li button:focus

{
  outline: none;
}

.slick-dots1 li button:hover:before,
.slick-dots1 li button:focus:before

{
  opacity: 1;
}

.slick-dots1 li button::before 
{
  font-family: 'slick';
  font-size: 5px;
  line-height: 10px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 7px;
  content: '•'; 
  text-align: center;
  color: #CCCCCC;
}

.slick-dots1 li.slick-active button:before
{
  color: #22283c;
  background-color: #22283c;
  border-radius: 5px;
}

