.icon-status {
  display: flex;
  align-items: center;
  margin-right: 5px;
  svg {
    width: 14px !important;
    height: 14px !important;
  }
}

.gap-detail-section {
  margin-right: 16px;
  display: flex;
  align-items: center;
}

.margin-icon {
  margin-right: 4px;
}

.rounded-circle {
  width: 16px;
  height: 16px;
  border-radius: 64px;
}

.trend-section {
  font-size: 12px !important;
  font-weight: 400 !important;
  margin: 0 !important;
}

.trend-down {
  color: #d7352b;
}

.trend-down-v2 {
  color: #0f8461;
}

.trend-up {
  color: #0f8461;
}

.trend-up-v2 {
  color: #d7352b;
}

.badge-number {
  width: fit-content;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 6px;
  border-radius: 8px;
  background-color: #eee;
  font-size: 12px;
  margin-left: 4px;
  color: #233145;
}

.browser-and-os-icon {
  width: 14px;
  height: 14px;

  svg {
    width: 14px;
    height: 14px;
  }
}

.status-bar {
  .status-bar-detail {
    display: inline-flex;
    min-width: 177px;
    height: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 2px 5px;
    border-radius: 4px;
    border: solid 1px #d5d8dd;
  
    .status-bar-detail-result {
      margin: 0 0.357rem;
      font-size: 12px;
      display: flex;
      font-weight: 500;
      align-items: center;  
      line-height: 1.67;

      .icon {
        margin-right: 5px;
        display: flex;
        align-items: center;
      }
  
      svg {
        width: 0.857rem;
        height: 0.857rem;
      }
  
      .icon-error-status-v2 {
        width: 1rem;
        height: 1rem;
      }
    }
  }
}

.line-status-passed {
  margin-right: 20px;
  width: 4px;
  background-color: #0f8461;
}

.line-status-failed {
  margin-right: 20px;
  width: 4px;
  background-color: #d7352b;
}

.line-status-incomplete {
  margin-right: 20px;
  width: 4px;
  background-color: #0f8461;
}

.line-status-none {
  margin-right: 24px;
}


