.MuiIconButton-root,
.MuiTouchRipple-child {
  border-radius: 50% !important;
}

.MuiTableRow-root.Mui-selected {
  background-color: rgba(245, 119, 0, 0.08) !important;
}

.MuiTableRow-root.Mui-selected:hover {
  background-color: rgba(245, 119, 0, 0.12) !important;
}

.MuiPaper-rounded {
  border-radius: 4px !important;
}

.MuiPaper-elevation5 {
  box-shadow: 0px 3px 5px -3px rgba(0, 0, 0, 0.07), 0px 5px 23px 0px rgba(0, 0, 0, 0.13), 0px 1px 26px 0px rgba(0, 0, 0, 0.06) !important;
}

.MuiSvgIcon-root {
  vertical-align: text-bottom;
}

.MuiIcon-root {
  width: auto;
  height: auto;
  font-size: 80%;
}

.MuiAvatar-root {
  border-radius: 50% !important;
}